<template>
    <div>
        <matte-video-widget :video="matte_video"></matte-video-widget>
        <div>
            <div class="pannel-widget">
                <h2 class="base-title-widget">图书馆</h2>
                <hr class="seg-widget"/>

                <!--侧边栏导航组件-->
                <ul class="aside-nav-widget" @click="sel_search_type">
                    <li class="an-w-btn" data-type="TEXT" state="act">文字</li>
                    <li class="an-w-btn" data-type="IMG" state="act">图片</li>
                    <li class="an-w-btn" data-type="VIDEO" state="act">视频</li>
                    <li class="an-w-btn" data-type="XMIND" state="act">导图</li>
                    <li class="an-w-btn" data-type="AUDIO" state="act">音频</li>
                    <li class="an-w-btn" data-type="SOFTWARE" state="act">软件</li>
                </ul>

            </div>
        </div>
        <div class="pannel-widget pad-w-2">

            <!--搜索栏组件-->
            <div class="search-widget">
                <div class="s-w-inp">
                    <i class="s-w-inp-ico icon-sousuo"></i>
                    <input class="s-w-inp-text" type="text" v-model="search_data.text"
                           placeholder="知识内容搜索 ..."/>
                </div>
                <span class="s-w-btn" @click="search">搜索</span>
            </div>

        </div>
        <div class="pad-h-1"></div>
        <div class="pannel-widget pad-w-2">

            <!--搜索结果-->
            <div class="search-result-widget">
                <ul v-if="search_point.length > 0" class="sr-w-result">
                    <li class="sr-w-item" v-for="item of search_point">
                        <span class="sr-w-item-title" @click="sel_point(item.title)">{{item.title}}</span>
                        <span class="sr-w-item-desc">{{item.desc}}</span>
                    </li>
                </ul>
                <div v-else-if="search_result">

                    <!--搜索结果-文本介绍-->
                    <div v-if="search_result.TEXT.length > 0">
                        <div v-for="text of search_result.TEXT" class="sr-w-res-item">
                            <div class="sr-w-res-item-title">{{text.title}}</div>
                            <div class="sr-w-res-item-p" v-html="text.desc"></div>
                        </div>
                    </div>

                    <!--搜索结果-图片类型-->
                    <div class="sr-w-res-item" v-if="search_result.IMG.length > 0">
                        <div class="sr-w-res-item-title">
                            图片
                            <!--<span class="sr-w-res-item-more">更多图片</span>-->
                        </div>
                        <div class="grid">
                            <div v-for="img of search_result.IMG"
                                 class="sr-w-res-item-img col-3 pad-right-1">
                                <a target="_blank" :href="img.src">
                                    <div class="sr-w-res-item-cover-box"><img
                                            class="sr-w-res-item-cover" :src="img.src"/>
                                    </div>
                                    <p class="sr-w-res-item-name">{{img.title}}</p>
                                </a>
                            </div>
                        </div>
                    </div>

                    <!--搜索结果-视频类型-->
                    <div class="sr-w-res-item" v-if="search_result.VIDEO.length > 0">
                        <div class="sr-w-res-item-title">
                            视频
                            <!--<span class="sr-w-res-item-more">更多视频</span>-->
                        </div>
                        <div class="grid">
                            <div v-for="video of search_result.VIDEO"
                                 class="sr-w-res-item-video col-12">
                                <span @click="open_video_matte(video.title, video.src)">
                                    <p><span>{{video.title}}</span><span class="sr-w-res-item-video-ico icon-bofang"></span></p>
                                </span>
                            </div>
                        </div>
                    </div>

                    <!--搜索结果-导图类型-->
                    <div class="sr-w-res-item" v-if="search_result.XMIND.length > 0">
                        <div class="sr-w-res-item-title">
                            思维导图
                            <!--<span class="sr-w-res-item-more">更多思维导图</span>-->
                        </div>
                        <div class="grid">
                            <div v-for="xmind of search_result.XMIND"
                                 class="sr-w-res-item-img col-3">
                                <a target="_blank" :href="xmind.src">
                                    <div class="sr-w-res-item-cover-box"><img
                                            class="sr-w-res-item-cover" :src="xmind.src"/>
                                    </div>
                                    <p class="sr-w-res-item-name">{{xmind.title}}</p>
                                </a>
                            </div>
                        </div>
                    </div>

                    <!--搜索结果-音频类型-->
                    <div class="sr-w-res-item" v-if="search_result.AUDIO.length > 0">
                        <div class="sr-w-res-item-title">
                            音频
                            <!--<span class="sr-w-res-item-more">更多音频</span>-->
                        </div>
                        <div class="grid">
                            <div v-for="audio of search_result.AUDIO"
                                 class="sr-w-res-item-img col-3">
                                <div class="sr-w-res-item-audio-box">
                                    <audio controls>
                                        <source src="/static/res/audio/audio_1.mp3"/>
                                    </audio>
                                </div>
                                <p class="sr-w-res-item-name">{{audio.title}}</p>
                            </div>
                        </div>
                    </div>

                    <!--搜索结果-软件类型-->
                    <div class="sr-w-res-item" v-if="search_result.SOFTWARE.length > 0">
                        <div class="sr-w-res-item-title">
                            软件
                            <!--<span class="sr-w-res-item-more">更多软件</span>-->
                        </div>
                        <div class="grid">
                            <div v-for="software of search_result.SOFTWARE"
                                 class="sr-w-res-item-img col-2d5">
                                <a href="/static/res/download/software_1.rar"
                                   download="download.rar">
                                    <div class="sr-w-res-item-cover-box"><img
                                            class="sr-w-res-item-cover"
                                            src="/static/res/software.jpg"/></div>
                                    <p class="sr-w-res-item-name">{{software.title}}</p>
                                </a>
                            </div>
                        </div>
                    </div>

                </div>
                <p v-else class="sr-w-no-result">
                    <i class="sr-w-no-result-ico icon-noresult"></i><br/>
                    <span>暂无搜索结果</span>
                </p>
            </div>

        </div>
    </div>
</template>

<script>
    import MatteVideo from "@/components/widget/matte-video-widget";

    export default {
        name: "library",
        data() {
            return {

                /*搜索内容（请求：1.知识点 -> 2.相关资源）*/
                search_data: {
                    type: ["TEXT", "IMG", "VIDEO", "XMIND", "AUDIO", "SOFTWARE"],
                    text: ""
                },

                /*搜索知识点*/
                search_point: [],

                /*搜索结果*/
                search_result: false,

                /*遮罩视频*/
                matte_video: {
                    state: "hide",
                    title: "",
                    video: ""
                },

                /*弹窗*/
                confirm: null,

            }
        },
        methods: {

            /*选择知识点资源类型*/
            sel_search_type(e) {
                let targ = e.target,
                    type;
                if (targ.nodeName.toLowerCase() === "li") {
                    type = targ.dataset.type;
                    if (targ.getAttribute("state") === "act") {//取消选中
                        targ.setAttribute("state", "");
                        this.search_data.type.splice(this.search_data.type.indexOf(type), 1);
                    } else {//选中
                        targ.setAttribute("state", "act");
                        this.search_data.type.push(type);
                    }
                }
            },

            /*搜索*/
            search() {
                let search_text = this.search_data.text,
                    search_text_arr = search_text.split("#");
                if (search_text == "") {//无搜索结果
                    this.search_point = [];
                    this.search_result = false;
                } else if (search_text_arr.length === 3) {//搜索知识点
                    search_text = search_text_arr[1];
                    this.search_as_point(search_text);
                } else {//搜索直接结果
                    this.search_as_result(search_text);
                }
            },

            /*选择知识点（搜索结果）*/
            sel_point(title) {
                this.search_as_result(title)
            },

            /*搜索知识点*/
            search_as_point(text) {
                this.$axios.get("library_search_tag/?search=" + text)
                    .then(res => {
                        this.search_point = this.$B.data_convert(res.data, {
                            point_id: "id",
                            title: "name",
                            desc: "intr",
                            name: 1,
                            father_tag: 1,
                        });
                    })
                this.search_result = false;//清空搜索结果
            },

            /*搜索结果*/
            search_as_result(text) {
                this.confirm.load("搜索中");
                this.$axios.get("library_search/?search=" + text)
                    .then(res => {
                        if (res.data.length > 0) {
                            this.search_result = {
                                TEXT: [],
                                IMG: [],
                                VIDEO: [],
                                XMIND: [],
                                AUDIO: [],
                                SOFTWARE: []
                            }
                            res.data.forEach(d => {

                                    /*文本介绍*/
                                    if (this.search_data.type.indexOf("TEXT") != -1) {
                                        this.search_result["TEXT"].push({
                                            text_id: d.id,
                                            title: d.name,
                                            desc: d.intr_text_pic,
                                        });
                                    }

                                    /*图片资源*/
                                    if (this.search_data.type.indexOf("IMG") != -1) {
                                        this.search_result["IMG"] = this.search_result["IMG"].concat(this.$B.data_convert(d.images, {
                                            img_id: "id",
                                            title: "name",
                                            src: "image",
                                        }));
                                    }

                                    /*音频资源*/
                                    if (this.search_data.type.indexOf("AUDIO") != -1) {
                                        this.search_result["AUDIO"] = this.search_result["AUDIO"].concat(this.$B.data_convert(d.audios, {
                                            audio_id: "id",
                                            title: "name",
                                            src: "image",
                                        }));
                                    }

                                    /*导图资源*/
                                    if (this.search_data.type.indexOf("XMIND") != -1) {
                                        this.search_result["XMIND"] = this.search_result["XMIND"].concat(this.$B.data_convert(d.minds, {
                                            xmind_id: "id",
                                            title: "name",
                                            src: "mind_image",
                                        }));
                                    }

                                    /*视频资源*/
                                    if (this.search_data.type.indexOf("VIDEO") != -1) {
                                        this.search_result["VIDEO"] = this.search_result["VIDEO"].concat(this.$B.data_convert(d.videos, {
                                            xmind_id: "id",
                                            title: "name",
                                            src: "video_url",
                                        }));
                                    }

                                }
                            )
                            this.confirm.load("搜索完成", "end");
                        } else {
                            this.search_result = false;//无搜索结果
                        }
                    })
                this.search_point = [];//清空搜索知识点
            },

            /*开启视频层*/
            open_video_matte(title, video){
                this.matte_video = {
                    state: "show",
                    title: title,
                    video: video
                };
            }

        },
        components: {
            "matte-video-widget": MatteVideo
        },
        mounted(){
            this.confirm = this.$B.widgets.Confirm;
            let search = this.$route.query.search;
            if(search){//默认搜索章节
                this.search_data.text = "#" + search + "#";
                this.search();
            }
        }
    }
</script>

<style scoped>
    @import "../../assets/css/pannel-widget.css";
    @import "../../assets/css/seg-widget.css";
    @import "../../assets/css/base-title-widget.css";
    @import "../../assets/css/aside-nav-widget.css";
    @import "../../assets/css/search-widget.css";
    @import "../../assets/css/search-result-widget.css";
</style>


